.yoshi {
  position: absolute;
  transform: translateY(-50%);
  animation: slide 10s linear infinite;
}

@keyframes slide {
  0% {
      left: 0;
  }
  100% {
      left: 445px;
  }
} 
    .iframe-container {
      width: 461px;
      height: 250px;
      margin-top: 5px;
      overflow: hidden;
      position: relative;
      box-shadow: -1px -1px #ffffff, 1px 1px #808080, 2px 2px #0a0a0a, -2px -2px #dfdfdf;
    }
    
    .scrollable-content {
      width: 100%;
      height: 100%;
      overflow: auto;
      border: none;
    }
    
    .scrollable-content::-webkit-scrollbar {
      width: 16px;
      height: 16px;
      background: none;
    }
    
    .scrollable-content::-webkit-scrollbar-thumb, 
    .scrollable-content::-webkit-scrollbar-button {
      width: 16px;
      height: 16px;
      background: silver;
      box-shadow: inset 1px 1px #dfdfdf, inset -1px -1px gray;
      border: 1px solid;
      border-color: silver #000 #000 silver;
    }
    
    .scrollable-content::-webkit-scrollbar-track {
      image-rendering: optimizeSpeed;
      image-rendering: pixelated;
      image-rendering: optimize-contrast;
      background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgLTAuNSAyIDIiIHNoYXBlLXJlbmRlcmluZz0iY3Jpc3BFZGdlcyI+CjxtZXRhZGF0YT5NYWRlIHdpdGggUGl4ZWxzIHRvIFN2ZyBodHRwczovL2NvZGVwZW4uaW8vc2hzaGF3L3Blbi9YYnh2Tmo8L21ldGFkYXRhPgo8cGF0aCBzdHJva2U9IiNjMGMwYzAiIGQ9Ik0wIDBoMU0xIDFoMSIgLz4KPC9zdmc+");
      background-position: 0 0;
      background-repeat: repeat;
      background-size: 2px;
    }
    
    .scrollable-content::-webkit-scrollbar-button {
      background-repeat: no-repeat;
      background-size: 16px;
    }
    
    .scrollable-content::-webkit-scrollbar-button:single-button:vertical:decrement {
      background-image: url("data:image/svg+xml;base64,");
    }
    
    .scrollable-content::-webkit-scrollbar-button:single-button:vertical:increment {
      background-image: url("data:image/svg+xml;base64,");
    }
    
    .scrollable-content::-webkit-scrollbar-button:single-button:horizontal:decrement {
      background-image: url("data:image/svg+xml;base64,");
    }
    
    .scrollable-content::-webkit-scrollbar-button:single-button:horizontal:increment {
      background-image: url("data:image/svg+xml;base64,");
    }
    
    .scrollable-content::-webkit-scrollbar-corner {
      background: silver;
    }

    .embedded-iframe {
      width: 100%;
      height: 2000px;
      border: none;
    }